import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import {
  IEmpCertificationGetResponseVM,
  IEmpCertificationVM,
} from "app/dashboard/models/employees/employee";
import { environment } from "environments/environment";
import { Observable } from "rxjs";

@Injectable({
  providedIn: "root",
})
export class EmpCertificationService {
  constructor(private http: HttpClient) {}

  // Return All EmpCertifications
  getAllCertificationsByEmployeeID(
    page: number,
    empId: number
  ): Observable<IEmpCertificationGetResponseVM> {
    return this.http.get<IEmpCertificationGetResponseVM>(
      `${environment.baseUrl}EmpCertifications/GetAllEmpCertifications/${page}/${empId}`
    );
  }

  // add EmpCertification
  addEmpCertification(empId: number, empCertification: IEmpCertificationVM) {
    return this.http.post(
      `${environment.baseUrl}EmpCertifications/addEmpCertification/${empId}`,
      empCertification
    );
  }

  // update EmpCertification
  updateEmpCertification(empId: number, empCertification: IEmpCertificationVM) {
    return this.http.put(
      `${environment.baseUrl}EmpCertifications/updateEmpCertification/${empId}`,
      empCertification
    );
  }

  // delete EmpCertification
  deleteEmpCertification(id: number) {
    return this.http.delete(
      `${environment.baseUrl}EmpCertifications/deleteEmpCertification/${id}`
    );
  }
}
