import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import {
  IEmpCourseAddEditVM,
  IEmpCourseGetResponseVM,
} from "app/dashboard/models/employees/employee";
import { environment } from "environments/environment";
import { Observable } from "rxjs";

@Injectable({
  providedIn: "root",
})
export class EmpCourseService {
  constructor(private http: HttpClient) {}

  // Return All EmpCourses
  getAllCoursesByEmployeeID(
    page: number,
    empId: number
  ): Observable<IEmpCourseGetResponseVM> {
    return this.http.get<IEmpCourseGetResponseVM>(
      `${environment.baseUrl}EmpCourses/GetAllEmpCourses/${page}/${empId}`
    );
  }

  // add EmpCourse
  addEmpCourse(empId: number, empCourse: IEmpCourseAddEditVM) {
    return this.http.post(
      `${environment.baseUrl}EmpCourses/addEmpCourse/${empId}`,
      empCourse
    );
  }

  // update EmpCourse
  updateEmpCourse(empId: number, empCourse: IEmpCourseAddEditVM) {
    return this.http.put(
      `${environment.baseUrl}EmpCourses/updateEmpCourse/${empId}`,
      empCourse
    );
  }

  // delete EmpCourse
  deleteEmpCourse(id: number) {
    return this.http.delete(
      `${environment.baseUrl}EmpCourses/deleteEmpCourse/${id}`
    );
  }
}
