import { AfterViewInit, Component, OnInit, ViewChild } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { MatDialog } from "@angular/material/dialog";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { ActivatedRoute } from "@angular/router";
import { TranslateService } from "@ngx-translate/core";
import {
  DepartmentAddEditVM,
  DepartmentGetVM,
} from "app/dashboard/models/employees/department";
import { DepartmentService } from "app/dashboard/services/employees/department.service";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { MessagesService } from "app/shared/services/messages.service";
import { IRowFunctionVM } from "app/shared/models/general/general";
import { NgxSpinnerService } from "ngx-spinner";
import { AuthService } from "app/shared/services/auth/auth.service";
import { CustomeValidator } from "app/shared/validators/customeValid.validator";

@Component({
  selector: "app-department",
  templateUrl: "./department.component.html",
  styleUrls: ["./department.component.scss"],
})
export class DepartmentComponent implements OnInit {
  Departments: DepartmentGetVM[] = [
    { Id: 1, Ar_Name: "", En_Name: "ff", EduCompId: 2 },
  ];
  myForm: FormGroup;
  submitted = false;
  curPage: number = 1;
  itemsCounts: number;
  currentLang: string;

  EduCompId: number;
  functionId: number;
  // rowFunctions: IRowFunctionVM[];
  rowFunctions: any[] = [{ functionName: "Edit" }, { functionName: "Delete" }];

  // table
  displayedColumns: string[] = ["#", "Ar_Name", "En_Name", "actions"];
  dataSource: MatTableDataSource<DepartmentGetVM>;

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(
    private fb: FormBuilder,
    private DepartmentServ: DepartmentService,
    private translate: TranslateService,
    private spinner: NgxSpinnerService,
    private ToastrMsg: MessagesService,
    private dialog: MatDialog,
    private route: ActivatedRoute,
    public authserv: AuthService
  ) {
    //use after auth for function id and row functions
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      // console.log("functionId", this.functionId);
      if (this.functionId) {
        this.authserv
          .getUserRowFunctions(this.functionId)
          .subscribe((res: any) => {
            if (res.length > 0) {
              // console.log("rowFunctions", res);
              this.rowFunctions = res;
            }
          });
      }
    });

    // current language
    this.translate.onLangChange.subscribe((lang) => {
      this.currentLang = lang.lang;
    });

    this.EduCompId = Number(authserv.getEduCompId());
  }

  ngOnInit(): void {
    this.getAllEmployeeDepartments(1);

    this.myForm = this.fb.group({
      Id: [null],
      Ar_Name: ["", [Validators.required, CustomeValidator.whiteSpace]],
      En_Name: ["", [Validators.required, CustomeValidator.whiteSpace]],
    });

    // test data
    this.dataSource = new MatTableDataSource(this.Departments);
  }

  ngAfterViewInit() {
    if (this.dataSource) {
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    }

    this.authserv.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      this.getAllEmployeeDepartments(1);
    });
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  get formCtrls() {
    return this.myForm.controls;
  }

  // get all departments
  getAllEmployeeDepartments(page: number) {
    this.curPage = page;
    this.spinner.show();

    this.DepartmentServ.getAllEmployeeDepartments(page).subscribe((res) => {
      if (res) {
        this.Departments = res.DepartmentList;
        this.dataSource = new MatTableDataSource(this.Departments);
        this.itemsCounts = res.countItems;
        console.log("Departments: >> ", res);
      }
      this.spinner.hide();
    });
  }

  onSubmit() {
    this.submitted = true;
    if (this.myForm.valid) {
      let department = {} as DepartmentAddEditVM;
      department.EduCompId = this.EduCompId;
      department.Ar_Name = this.formCtrls.Ar_Name?.value;
      department.En_Name = this.formCtrls.En_Name?.value;
      console.log("department", department);

      this.formCtrls.Id?.value
        ? this.updateDepartment(department)
        : this.addDepartment(department);
    } else this.ToastrMsg.error("General.FormNotValid");
  }

  // add department
  addDepartment(department: DepartmentAddEditVM) {
    this.spinner.show();
    this.DepartmentServ.addEmployeeDepartment(department).subscribe(
      (res: any) => {
        if (res?.returnValue == 1) {
          this.dataSource.data.push(res);
          this.itemsCounts++;
          // this.getAllEmployeeDepartments(this.curPage);
          this.ToastrMsg.success("General.AddSuccessfully");

          this.myForm.reset();
          this.submitted = false;
        } else {
          this.ToastrMsg.error(res?.returnString);
        }
        this.spinner.hide();
      }
    );
  }

  // get form values to update
  toEditDepartment(department: DepartmentGetVM) {
    this.myForm.patchValue(department);
  }

  // update department
  updateDepartment(department: DepartmentAddEditVM) {
    this.spinner.show();
    this.DepartmentServ.updateEmployeeDepartment(
      this.formCtrls.Id.value,
      department
    ).subscribe((res: any) => {
      if (res?.returnValue == 1) {
        let i = this.dataSource.data.findIndex(
          (e) => e.Id == this.formCtrls.Id.value
        );
        this.dataSource.data[i] = res;

        this.ToastrMsg.success("General.UpdateSuccessfully");
        this.myForm.reset();
        this.submitted = false;
      } else {
        this.ToastrMsg.error(res?.returnString);
      }
      this.spinner.hide();
    });
  }

  // delete dialog
  openDeleteDialog(department: DepartmentGetVM): void {
    let departmentName =
      this.currentLang == "ar" ? department.Ar_Name : department.En_Name;

    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: departmentName,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.deleteDepartment(department);
      });
  }

  // delete department
  deleteDepartment(department: DepartmentGetVM) {
    this.spinner.show();
    this.DepartmentServ.deleteEmployeeDepartment(department.Id).subscribe(
      (res: any) => {
        if (res?.returnValue == 1) {
          let i = this.dataSource.data.findIndex((e) => e == department);
          this.dataSource.data.splice(i, 1);

          this.ToastrMsg.info("General.DeleteSuccessfully");
        } else {
          this.ToastrMsg.error(res?.returnString);
        }
        this.spinner.hide();
      }
    );
  }
}
