import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import {
  DepartmentAddEditVM,
  DepartmentGetResponseVM,
  DepartmentGetVM,
} from "app/dashboard/models/employees/department";
import { environment } from "environments/environment";
import { Observable } from "rxjs";

@Injectable({
  providedIn: "root",
})
export class DepartmentService {
  constructor(private http: HttpClient) {}

  // Return All  Departments by page
  getAllEmployeeDepartments(page: number): Observable<DepartmentGetResponseVM> {
    return this.http.get<DepartmentGetResponseVM>(
      `${environment.baseUrl}Departments/getAllEmployeeDepartments/${page}`
    );
  }

  // Return All Departments > dropDown list
  getEmployeeDepartmentsDropDown(): Observable<DepartmentGetVM[]> {
    return this.http.get<DepartmentGetVM[]>(
      `${environment.baseUrl}Departments/getAllDepartmentsDropDown`
    );
  }

  //Add New Department
  addEmployeeDepartment(data: DepartmentAddEditVM) {
    return this.http.post(
      `${environment.baseUrl}Departments/addDepartment`,
      data
    );
  }

  // Update Department
  updateEmployeeDepartment(id: number, data: DepartmentAddEditVM) {
    return this.http.put(
      `${environment.baseUrl}Departments/updateDepartment${id}`,
      data
    );
  }

  //Delete Department
  deleteEmployeeDepartment(id: number) {
    return this.http.delete(
      `${environment.baseUrl}Departments/deleteDepartment/${id}`
    );
  }
}
