import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import {
  IAttachmentAddEditVM,
  IAttachmentResponseVM,
} from "app/dashboard/models/setup/general-setup/attach-type";
import { environment } from "environments/environment";
import { Observable } from "rxjs";
import { map, shareReplay } from "rxjs/operators";

@Injectable({
  providedIn: "root",
})
export class EmpAttachmentService {
  constructor(private http: HttpClient) {}

  // Return All EmpAttachments
  getAllAttachmentsByEmployeeID(
    page: number,
    empId: number
  ): Observable<IAttachmentResponseVM> {
    return this.http.get<IAttachmentResponseVM>(
      `${environment.baseUrl}EmpAttachments/GetAllEmpAttachments/${page}/${empId}`
    );
  }

  // add EmpAttachment
  addEmpAttachment(empId: number, empAttachment: IAttachmentAddEditVM) {
    return this.http.post(
      `${environment.baseUrl}EmpAttachments/addEmpAttachment/${empId}`,
      empAttachment
    );
  }

  // update EmpAttachment
  updateEmpAttachment(empId: number, empAttachment: IAttachmentAddEditVM) {
    return this.http.put(
      `${environment.baseUrl}EmpAttachments/updateEmpAttachment/${empId}`,
      empAttachment
    );
  }

  // delete EmpAttachment
  deleteEmpAttachment(id: number) {
    return this.http.delete(
      `${environment.baseUrl}EmpAttachments/deleteEmpAttachment/${id}`
    );
  }
}
