import { Component, Input, OnInit, ViewChild } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { MatDialog } from "@angular/material/dialog";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { ActivatedRoute } from "@angular/router";
import { TranslateService } from "@ngx-translate/core";
import { IEmpExperienceHistoryVM } from "app/dashboard/models/employees/employee";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { MessagesService } from "app/shared/services/messages.service";
import { IRowFunctionVM } from "app/shared/models/general/general";
import { NgxSpinnerService } from "ngx-spinner";
import { AuthService } from "app/shared/services/auth/auth.service";
import { EmpExperienceHistoryService } from "app/dashboard/services/employees/emp-experience-history.service";
import { CustomeValidator } from "app/shared/validators/customeValid.validator";

@Component({
  selector: "app-employee-experience-history",
  templateUrl: "./employee-experience-history.component.html",
  styleUrls: ["./employee-experience-history.component.scss"],
})
export class EmployeeExperienceHistoryComponent implements OnInit {
  @Input("EmployeeID") EmployeeID: number;

  EmpExperiences: IEmpExperienceHistoryVM[] = [
    {
      Id: 2,
      EduCompId: 2,
      FromDate: new Date(),
      JobTitle: "sx",
      Place: "Sx",
      ToDate: new Date(),
    },
  ];

  myForm: FormGroup;
  submitted = false;
  curPage: number = 1;
  itemsCounts: number;
  currentLang: string;

  EduCompId: number;
  functionId: number;
  // rowFunctions: IRowFunctionVM[];
  rowFunctions: any[] = [{ functionName: "Edit" }, { functionName: "Delete" }];

  // table
  displayedColumns: string[] = [
    "#",
    "Place",
    "JobTitle",
    "FromDate",
    "ToDate",
    "actions",
  ];
  dataSource: MatTableDataSource<IEmpExperienceHistoryVM>;

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(
    private fb: FormBuilder,
    private EmpExperienceServ: EmpExperienceHistoryService,
    private translate: TranslateService,
    private spinner: NgxSpinnerService,
    private ToastrMsg: MessagesService,
    private dialog: MatDialog,
    private route: ActivatedRoute,
    public authserv: AuthService
  ) {
    //use after auth for function id and row functions
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      // console.log("functionId", this.functionId);
      if (this.functionId) {
        this.authserv
          .getUserRowFunctions(this.functionId)
          .subscribe((res: any) => {
            if (res.length > 0) {
              // console.log("rowFunctions", res);
              this.rowFunctions = res;
            }
          });
      }
    });

    // current language
    this.translate.onLangChange.subscribe((lang) => {
      this.currentLang = lang.lang;
    });

    this.EduCompId = Number(authserv.getEduCompId());
  }

  ngOnInit(): void {
    if (this.EmployeeID) this.getAllExperiences(1);

    this.myForm = this.fb.group({
      Id: [null],
      Place: ["", [Validators.required, CustomeValidator.whiteSpace]],
      JobTitle: ["", [Validators.required, CustomeValidator.whiteSpace]],
      FromDate: ["", [Validators.required]],
      ToDate: ["", [Validators.required]],
    });

    // test
    this.dataSource = new MatTableDataSource(this.EmpExperiences);
  }

  ngAfterViewInit() {
    if (this.dataSource) {
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    }

    this.authserv.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      if (this.EmployeeID) this.getAllExperiences(1);
    });

    // console.log(this.EmployeeID);
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  get formCtrls() {
    return this.myForm.controls;
  }

  // get all employee Experiences
  getAllExperiences(page: number) {
    this.curPage = page;
    this.spinner.show();

    this.EmpExperienceServ.getAllExperienceHistorysByEmployeeID(
      page,
      this.EmployeeID
    ).subscribe((res) => {
      if (res) {
        this.EmpExperiences = res.EmpExperienceList;
        this.dataSource = new MatTableDataSource(this.EmpExperiences);
        this.itemsCounts = res.countItems;
        console.log("Experiences: >> ", res);
      }
      this.spinner.hide();
    });
  }

  onSubmit() {
    this.submitted = true;
    if (this.myForm.valid) {
      let employeeExperience = {} as IEmpExperienceHistoryVM;
      employeeExperience.EduCompId = this.EduCompId;
      employeeExperience.Id = this.formCtrls.Id?.value;
      employeeExperience.Place = this.formCtrls.Place?.value;
      employeeExperience.JobTitle = this.formCtrls.JobTitle?.value;
      employeeExperience.FromDate = this.formCtrls.FromDate?.value;
      employeeExperience.ToDate = this.formCtrls.ToDate?.value;
      console.log("employeeExperience", employeeExperience);

      this.formCtrls.Id?.value
        ? this.updateExperienceHistory(employeeExperience)
        : this.addExperienceHistory(employeeExperience);
    } else this.ToastrMsg.error("General.FormNotValid");
  }

  // add employee Experience
  addExperienceHistory(employeeExperience: IEmpExperienceHistoryVM) {
    this.spinner.show();
    this.EmpExperienceServ.addEmpExperienceHistory(
      this.EmployeeID,
      employeeExperience
    ).subscribe((res: any) => {
      if (res?.returnValue == 1) {
        this.dataSource.data.push(res);
        this.itemsCounts++;
        // this.getAllExperiences(this.curPage);
        this.ToastrMsg.success("General.AddSuccessfully");

        this.myForm.reset();
        this.submitted = false;
      } else {
        this.ToastrMsg.error(res?.returnString);
      }
      this.spinner.hide();
    });
  }

  // get form values to update
  toEditExperience(employeeExperience: IEmpExperienceHistoryVM) {
    this.myForm.patchValue(employeeExperience);
  }

  // update employee Experience
  updateExperienceHistory(employeeExperience: IEmpExperienceHistoryVM) {
    this.spinner.show();
    this.EmpExperienceServ.updateEmpExperienceHistory(
      this.EmployeeID,
      employeeExperience
    ).subscribe((res: any) => {
      if (res?.returnValue == 1) {
        let i = this.dataSource.data.findIndex(
          (e) => e.Id == this.formCtrls.Id.value
        );
        this.dataSource.data[i] = res;

        this.ToastrMsg.success("General.UpdeteSuccessfully");
        this.myForm.reset();
        this.submitted = false;
      } else {
        this.ToastrMsg.error(res?.returnString);
      }
      this.spinner.hide();
    });
  }

  // delete dialog
  openDeleteDialog(employeeExperience: IEmpExperienceHistoryVM): void {
    let title = `${employeeExperience.Place} - ${employeeExperience.JobTitle}`;

    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: title,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.deleteExperience(employeeExperience);
      });
  }

  // delete employeeExperience
  deleteExperience(employeeExperience: IEmpExperienceHistoryVM) {
    this.spinner.show();
    this.EmpExperienceServ.deleteEmpExperienceHistory(
      employeeExperience.Id
    ).subscribe((res: any) => {
      if (res?.returnValue == 1) {
        let i = this.dataSource.data.findIndex((e) => e == employeeExperience);
        this.dataSource.data.splice(i, 1);

        this.ToastrMsg.info("General.DeleteSuccessfully");
      } else {
        this.ToastrMsg.error(res?.returnString);
      }
      this.spinner.hide();
    });
  }
}
