import { Component, ElementRef, OnInit, ViewChild } from "@angular/core";
import { MatDialog } from "@angular/material/dialog";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { ActivatedRoute } from "@angular/router";
import { TranslateService } from "@ngx-translate/core";
import {
  ILoadFPGetVM,
  IUploadFPVM,
} from "app/dashboard/models/employees/fingerprint";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { MessagesService } from "app/shared/services/messages.service";
import { IfileVM, IRowFunctionVM } from "app/shared/models/general/general";
import { NgxSpinnerService } from "ngx-spinner";
import { AuthService } from "app/shared/services/auth/auth.service";
import { FingerprintService } from "app/dashboard/services/employees/fingerprint.service";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";

@Component({
  selector: "app-load-fingerprint-index",
  templateUrl: "./load-fingerprint-index.component.html",
  styleUrls: ["./load-fingerprint-index.component.scss"],
})
export class LoadFingerprintIndexComponent implements OnInit {
  @ViewChild("fileInput") fileInput: ElementRef;

  EmpFPs: ILoadFPGetVM[] = [
    {
      Id: 1,
      EduCompId: 2,
      Device: "dd",
      BudgeNumber: 22,
      CompanyId: 2,
      Date: new Date(),
      Employee: {
        Address: "n",
        Code: "sd",
        CountryID: 1,
        EduCompId: 2,
        Email: "bn",
        Id: 1,
        Mobile: "011521555",
        Name: "ss",
        ar_name: "ss",
        en_name: "ss",

      },
    },
  ];
  Devices: any[] = [{ Id: 1, Name: "ff" }];

  myForm: FormGroup;
  submitted = false;

  curPage: number = 1;
  itemsCounts: number;
  currentLang: string;

  EduCompId: number;
  functionId: number;
  // rowFunctions: IRowFunctionVM[];
  rowFunctions: any[] = [
    /* { functionName: "Edit" },  */ { functionName: "Delete" },
  ];

  // table
  displayedColumns: string[] = [
    "#",
    "Employee",
    "BudgeNumber",
    "Date",
    "Device",
    "CompanyId",
    "actions",
  ];
  dataSource: MatTableDataSource<ILoadFPGetVM>;

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(
    private fb: FormBuilder,
    private FingerprintServ: FingerprintService,
    private translate: TranslateService,
    private spinner: NgxSpinnerService,
    private ToastrMsg: MessagesService,
    private dialog: MatDialog,
    private route: ActivatedRoute,
    public authserv: AuthService
  ) {
    //use after auth for function id and row functions
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      // console.log("functionId", this.functionId);
      if (this.functionId) {
        this.authserv
          .getUserRowFunctions(this.functionId)
          .subscribe((res: any) => {
            if (res.length > 0) {
              // console.log("rowFunctions", res);
              this.rowFunctions = res;
            }
          });
      }
    });

    // current language
    this.translate.onLangChange.subscribe((lang) => {
      this.currentLang = lang.lang;
    });

    this.EduCompId = Number(authserv.getEduCompId());
  }

  ngOnInit(): void {
    this.getAllEmpFPs(1);

    this.myForm = this.fb.group({
      Id: [null],
      DeviceId: ["", [Validators.required]],
      UploadedData: ["", [Validators.required]],
    });

    // test
    this.dataSource = new MatTableDataSource(this.EmpFPs);
  }

  ngAfterViewInit() {
    if (this.dataSource) {
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    }

    this.authserv.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      this.getAllEmpFPs(1);
    });
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  get formCtrls() {
    return this.myForm.controls;
  }

  // get all devices
  getAllDevices() {
    this.spinner.show();
    this.FingerprintServ.getAllDevices().subscribe((res) => {
      if (res) this.Devices = res;
      this.spinner.hide();
    });
  }

  // get all Employee EmpFPs
  getAllEmpFPs(page: number) {
    this.curPage = page;
    this.spinner.show();
    this.FingerprintServ.getAllLoadFPs(page).subscribe((res) => {
      if (res) {
        this.EmpFPs = res.FingerprintsList;
        this.dataSource = new MatTableDataSource(this.EmpFPs);
        this.itemsCounts = res.countItems;
        console.log("EmpFPs: >> ", res);
      }
      this.spinner.hide();
    });
  }

  // add Employee EmpFP
  addEmpFP() {
    this.submitted = true;
    if (this.myForm.valid) {
      let EmpFP = {} as IUploadFPVM;
      EmpFP.EduCompId = this.EduCompId;
      EmpFP.UploadedData = this.formCtrls.UploadedData?.value;
      EmpFP.DeviceId = this.formCtrls.DeviceId?.value;
      console.log("EmpFP", EmpFP);

      this.spinner.show();
      this.FingerprintServ.uploadFP(EmpFP).subscribe((res: any) => {
        if (res?.returnValue == 1) {
          this.dataSource.data.push(res);
          this.itemsCounts++;
          // this.getAllEmpFPs(this.curPage);
          this.ToastrMsg.success("General.AddSuccessfully");
          this.myForm.reset();
          this.submitted = false;
        } else {
          this.ToastrMsg.error(res?.returnString);
        }
        this.spinner.hide();
      });
    } else this.ToastrMsg.error("General.FormNotValid");
  }

  // on select file
  onChangeFile(event: any) {
    let fileName = event.target.files[0].name;
    let fileSize = event.target.files[0].size;
    let fileType = event.target.files[0].type;
    let LastModified = event.target.files[0].lastModified;
    let LastModifiedDate = event.target.files[0].lastModifiedDate;

    if (event.target.files) {
      var reader = new FileReader();

      reader.readAsDataURL(event.target.files[0]);
      reader.onload = (event: any) => {
        let fileReder = event.target.result;

        let data: IfileVM = {
          FileAsBase64: fileReder.replace(
            "data:application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;base64,",
            ""
          ),
          name: fileName,
          size: fileSize,
          type: fileType,
          LastModified: LastModified,
          LastModifiedDate: LastModifiedDate,
        };
        this.formCtrls?.UploadedData?.setValue(data);
      };
    }
  }

  RemoveFile() {
    this.formCtrls.UploadedData.setValue("");
    this.fileInput.nativeElement.value = "";
  }

  downloadTemplate() {}

  // delete dialog
  openDeleteDialog(EmpFP: ILoadFPGetVM): void {
    // let title = `${EmpFP.Employee.Name} - ${EmpFP.Date}`;
    let title = `${EmpFP.Employee.Name}`;

    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: title,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.deleteEmpFP(EmpFP);
      });
  }

  // delete EmpFP
  deleteEmpFP(EmpFP: ILoadFPGetVM) {
    this.spinner.show();
    this.FingerprintServ.deleteLoadFP(EmpFP.Id).subscribe((res: any) => {
      if (res?.returnValue == 1) {
        let i = this.dataSource.data.findIndex((e) => e == EmpFP);
        this.dataSource.data.splice(i, 1);
        this.ToastrMsg.info("General.DeleteSuccessfully");
      } else {
        this.ToastrMsg.error(res?.returnString);
      }
      this.spinner.hide();
    });
  }
}
