import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { EmployeesRoutingModule } from './employees-routing.module';
import { DepartmentComponent } from './department/department.component';
import { SharedModule } from 'app/shared/shared.module';
import { NgxSpinnerModule } from 'ngx-spinner';
import { MaterialModule } from 'app/shared/material/material.module';
import { ReactiveFormsModule } from '@angular/forms';
import { JobTitleComponent } from './job-title/job-title.component';
import { EmployeeIndexComponent } from './employee/employee-index/employee-index.component';
import { AddEmployeeComponent } from './employee/add-employee/add-employee.component';
import { EmployeeJobInfoComponent } from './employee/employee-job-info/employee-job-info.component';
import { EmployeeCoursesComponent } from './employee/employee-courses/employee-courses.component';
import { EmployeeExperienceHistoryComponent } from './employee/employee-experience-history/employee-experience-history.component';
import { EmployeeAttachmentsComponent } from './employee/employee-attachments/employee-attachments.component';
import { EmployeeCertificationsComponent } from './employee/employee-certifications/employee-certifications.component';
import { LoadFingerprintIndexComponent } from './attendance/load-fingerprint-index/load-fingerprint-index.component';
import { RegisterFingerprintComponent } from './attendance/register-fingerprint/register-fingerprint.component';
import { UploadFingerprintComponent } from './attendance/upload-fingerprint/upload-fingerprint.component';
import { DateAdapter, MAT_DATE_FORMATS } from "@angular/material/core";
import {
  AppDateAdapter,
  APP_DATE_FORMATS,
} from "app/shared/validators/date.adapter";

@NgModule({
  declarations: [
    DepartmentComponent,
    JobTitleComponent,
    EmployeeIndexComponent,
    AddEmployeeComponent,
    EmployeeJobInfoComponent,
    EmployeeCoursesComponent,
    EmployeeExperienceHistoryComponent,
    EmployeeAttachmentsComponent,
    EmployeeCertificationsComponent,
    LoadFingerprintIndexComponent,
    RegisterFingerprintComponent,
    UploadFingerprintComponent,
  ],
  imports: [
    CommonModule,
    SharedModule,
    NgxSpinnerModule,
    MaterialModule,
    ReactiveFormsModule,
    EmployeesRoutingModule
  ],
  providers: [
    {
      provide: DateAdapter,
      useClass: AppDateAdapter,
    },
    {
      provide: MAT_DATE_FORMATS,
      useValue: APP_DATE_FORMATS,
    },
  ],
})
export class EmployeesModule { }
