import { Observable } from "rxjs";
import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import {
  AttachTypeAddEditVM,
  AttachTypeGetResponseVM,
  AttachTypeGetVM,
  IAttachTypeGetByUserType,
  IAttachTypeGetDropdown,
  IAttachUserTypeAddEditVM,
  IAttachUserTypeGetVM,
} from "app/dashboard/models/setup/general-setup/attach-type";
import { environment } from "environments/environment";

@Injectable({
  providedIn: "root",
})
export class AttachTypeService {
  constructor(private http: HttpClient) {}

  // Return All  AttachTypes by page
  getAllAttachTypes(
    page: number,
    eduCompId: number
  ): Observable<AttachTypeGetResponseVM> {
    return this.http.get<AttachTypeGetResponseVM>(
      `${environment.baseUrl}AttachType/getAttachTypeByEduCompId/${eduCompId}/${page}`
    );
  }

  // Return All  AttachTypes > dropDown list
  getAttachTypesDropDown(eduCompId: number) {
    return this.http.get<IAttachTypeGetDropdown[]>(
      `${environment.baseUrl}AttachType/getAllAttachType/${eduCompId}`
    );
  }

  // Return All  AttachTypes > dropDown list
  getAttachTypesDropDownByUserId(userId: number) {
    return this.http.get<{ attachmentList: IAttachTypeGetByUserType[] }>(
      `${environment.baseUrl}AttachType/GetAttachmentByStudentUserId/${userId}`
    );
  }

  //Add New AttachType
  addEditAttachType(data: AttachTypeAddEditVM) {
    return this.http.post(
      `${environment.baseUrl}AttachType/addEditAttachType`,
      data
    );
  }

  //Delete AttachType
  deleteAttachType(id: number) {
    return this.http.delete(
      `${environment.baseUrl}AttachType/deleteAttachType/${id}`
    );
  }

  ////////// add user types to attachments
  // Return All  UserTypes
  getAllUserTypesByAttachId(attachTypeId: number) {
    return this.http.get<{ attachUserTypeList: IAttachUserTypeGetVM[] }>(
      `${environment.baseUrl}UserType/GetAllAttachUserTypeByAttachTypeId/${attachTypeId}`
    );
  }

  //Add UserTypes
  addUserTypes(data: IAttachUserTypeAddEditVM) {
    return this.http.post(
      `${environment.baseUrl}AttachType/AddAttachUserType`,
      data
    );
  }

  EditUserTypes(data: IAttachUserTypeAddEditVM) {
    return this.http.put(
      `${environment.baseUrl}AttachType/EditAttachUserType`,
      data
    );
  }

  //Delete UserTypes
  deleteUserTypes(id: number) {
    return this.http.delete(
      `${environment.baseUrl}Student/deleteAttachuserType/${id}`
    );
  }
}
