import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import {
  JobTitleAddEditVM,
  JobTitleGetResponseVM,
  JobTitleGetVM,
} from "app/dashboard/models/employees/job-title";
import { environment } from "environments/environment";
import { Observable } from "rxjs";

@Injectable({
  providedIn: "root",
})
export class JobTitleService {
  constructor(private http: HttpClient) {}

  // Return All JobTitles by page
  getAllEmployeeJobTitles(page: number): Observable<JobTitleGetResponseVM> {
    return this.http.get<JobTitleGetResponseVM>(
      `${environment.baseUrl}JobTitles/getAllEmployeeJobTitles/${page}`
    );
  }

  // Return All JobTitles > dropDown list
  getEmployeeJobTitlesDropDown(): Observable<JobTitleGetVM[]> {
    return this.http.get<JobTitleGetVM[]>(
      `${environment.baseUrl}JobTitles/getAllJobTitlesDropDown`
    );
  }

  // Add New JobTitle
  addEmployeeJobTitle(data: JobTitleAddEditVM) {
    return this.http.post(`${environment.baseUrl}JobTitles/addJobTitle`, data);
  }

  // Update JobTitle
  updateEmployeeJobTitle(id: number, data: JobTitleAddEditVM) {
    return this.http.put(
      `${environment.baseUrl}JobTitles/updateJobTitle${id}`,
      data
    );
  }

  // Delete JobTitle
  deleteEmployeeJobTitle(id: number) {
    return this.http.delete(
      `${environment.baseUrl}JobTitles/deleteJobTitle/${id}`
    );
  }
}
