import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import {
  IEmpExperienceHistoryGetResponseVM,
  IEmpExperienceHistoryVM,
} from "app/dashboard/models/employees/employee";
import { environment } from "environments/environment";
import { Observable } from "rxjs";

@Injectable({
  providedIn: "root",
})
export class EmpExperienceHistoryService {
  constructor(private http: HttpClient) {}

  // Return All EmpExperienceHistorys
  getAllExperienceHistorysByEmployeeID(
    page: number,
    empId: number
  ): Observable<IEmpExperienceHistoryGetResponseVM> {
    return this.http.get<IEmpExperienceHistoryGetResponseVM>(
      `${environment.baseUrl}EmpExperienceHistorys/GetAllEmpExperienceHistorys/${page}/${empId}`
    );
  }

  // add EmpExperienceHistory
  addEmpExperienceHistory(
    empId: number,
    empExperienceHistory: IEmpExperienceHistoryVM
  ) {
    return this.http.post(
      `${environment.baseUrl}EmpExperienceHistorys/addEmpExperienceHistory/${empId}`,
      empExperienceHistory
    );
  }

  // update EmpExperienceHistory
  updateEmpExperienceHistory(
    empId: number,
    empExperienceHistory: IEmpExperienceHistoryVM
  ) {
    return this.http.put(
      `${environment.baseUrl}EmpExperienceHistorys/updateEmpExperienceHistory/${empId}`,
      empExperienceHistory
    );
  }

  // delete EmpExperienceHistory
  deleteEmpExperienceHistory(id: number) {
    return this.http.delete(
      `${environment.baseUrl}EmpExperienceHistorys/deleteEmpExperienceHistory/${id}`
    );
  }
}
