import { Component, Input, OnInit, ViewChild } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { MatDialog } from "@angular/material/dialog";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { ActivatedRoute } from "@angular/router";
import { TranslateService } from "@ngx-translate/core";
import {
  IEmpJobInfoAddEditVM,
  IEmpJobInfoGetVM,
} from "app/dashboard/models/employees/employee";
import { JobTitleGetVM } from "app/dashboard/models/employees/job-title";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { MessagesService } from "app/shared/services/messages.service";
import { IRowFunctionVM } from "app/shared/models/general/general";
import { NgxSpinnerService } from "ngx-spinner";
import { AuthService } from "app/shared/services/auth/auth.service";
import { DepartmentGetVM } from "app/dashboard/models/employees/department";
import { EmpJobInfoService } from "app/dashboard/services/employees/emp-job-info.service";
import { JobTitleService } from "app/dashboard/services/employees/job-title.service";
import { DepartmentService } from "app/dashboard/services/employees/department.service";

@Component({
  selector: "app-employee-job-info",
  templateUrl: "./employee-job-info.component.html",
  styleUrls: ["./employee-job-info.component.scss"],
})
export class EmployeeJobInfoComponent implements OnInit {
  @Input("EmployeeID") EmployeeID: number;

  EmpJobInfos: IEmpJobInfoGetVM[] = [
    {
      Id: 1,
      EduCompId: 2,
      Department: { Ar_Name: "ss", EduCompId: 2, En_Name: "ss", Id: 2 },
      FromDate: new Date(),
      JobTitle: { Ar_Name: "ss", EduCompId: 2, En_Name: "ss", Id: 2 },
      ToDate: new Date(),
    },
  ];

  Departments: DepartmentGetVM[] = [];
  JobTitles: JobTitleGetVM[] = [];

  myForm: FormGroup;
  submitted = false;
  curPage: number = 1;
  itemsCounts: number;
  currentLang: string;

  EduCompId: number;
  functionId: number;
  // rowFunctions: IRowFunctionVM[];
  rowFunctions: any[] = [{ functionName: "Edit" }, { functionName: "Delete" }];

  // table
  displayedColumns: string[] = [
    "#",
    "Department",
    "JobTitle",
    "FromDate",
    "ToDate",
    "actions",
  ];
  dataSource: MatTableDataSource<IEmpJobInfoGetVM>;

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(
    private fb: FormBuilder,
    private EmpJobInfoServ: EmpJobInfoService,
    private JobTitleServ: JobTitleService,
    private DepartmentServ: DepartmentService,
    private translate: TranslateService,
    private spinner: NgxSpinnerService,
    private ToastrMsg: MessagesService,
    private dialog: MatDialog,
    private route: ActivatedRoute,
    public authserv: AuthService
  ) {
    //use after auth for function id and row functions
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      // console.log("functionId", this.functionId);
      if (this.functionId) {
        this.authserv
          .getUserRowFunctions(this.functionId)
          .subscribe((res: any) => {
            if (res.length > 0) {
              // console.log("rowFunctions", res);
              this.rowFunctions = res;
            }
          });
      }
    });

    // current language
    this.translate.onLangChange.subscribe((lang) => {
      this.currentLang = lang.lang;
    });

    this.EduCompId = Number(authserv.getEduCompId());
  }

  ngOnInit(): void {
    if (this.EmployeeID) this.getAllJobInfos(1);
    this.getAllDepartments();
    this.getAllJobTitles();

    this.myForm = this.fb.group({
      Id: [null],
      DepartmentId: ["", [Validators.required]],
      JobTitleId: ["", [Validators.required]],
      FromDate: ["", [Validators.required]],
      ToDate: ["", [Validators.required]],
    });

    // test
    this.dataSource = new MatTableDataSource(this.EmpJobInfos);
  }

  ngAfterViewInit() {
    if (this.dataSource) {
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    }

    this.authserv.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      if (this.EmployeeID) this.getAllJobInfos(1);
      this.getAllDepartments();
      this.getAllJobTitles();
    });

    // console.log(this.EmployeeID);
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  get formCtrls() {
    return this.myForm.controls;
  }

  // get all employee JobInfos
  getAllJobInfos(page: number) {
    this.curPage = page;
    this.spinner.show();

    this.EmpJobInfoServ.getAllJobInfosByEmployeeID(
      page,
      this.EmployeeID
    ).subscribe((res) => {
      if (res) {
        this.EmpJobInfos = res.EmpJobInfosList;
        this.dataSource = new MatTableDataSource(this.EmpJobInfos);
        this.itemsCounts = res.countItems;
        console.log("JobInfos: >> ", res);
      }
      this.spinner.hide();
    });
  }

  getAllDepartments() {
    this.spinner.show();
    this.DepartmentServ.getEmployeeDepartmentsDropDown().subscribe((res) => {
      if (res) this.Departments = res;
      this.spinner.hide();
    });
  }

  getAllJobTitles() {
    this.spinner.show();
    this.JobTitleServ.getEmployeeJobTitlesDropDown().subscribe((res) => {
      if (res) this.JobTitles = res;
      this.spinner.hide();
    });
  }

  onSubmit() {
    this.submitted = true;
    if (this.myForm.valid) {
      let employeeJobInfo = {} as IEmpJobInfoAddEditVM;
      employeeJobInfo.EduCompId = this.EduCompId;
      employeeJobInfo.Id = this.formCtrls.Id?.value;
      employeeJobInfo.DepartmentId = this.formCtrls.DepartmentId?.value;
      employeeJobInfo.JobTitleId = this.formCtrls.JobTitleId?.value;
      employeeJobInfo.FromDate = this.formCtrls.FromDate?.value;
      employeeJobInfo.ToDate = this.formCtrls.ToDate?.value;
      console.log("employeeJobInfo", employeeJobInfo);

      this.formCtrls.Id?.value
        ? this.updateJobInfo(employeeJobInfo)
        : this.addJobInfo(employeeJobInfo);
    } else this.ToastrMsg.error("General.FormNotValid");
  }

  // add employee JobInfo
  addJobInfo(employeeJobInfo: IEmpJobInfoAddEditVM) {
    this.spinner.show();
    this.EmpJobInfoServ.addEmpJobInfo(
      this.EmployeeID,
      employeeJobInfo
    ).subscribe((res: any) => {
      if (res?.returnValue == 1) {
        this.dataSource.data.push(res);
        this.itemsCounts++;
        // this.getAllJobInfos(this.curPage);
        this.ToastrMsg.success("General.AddSuccessfully");

        this.myForm.reset();
        this.submitted = false;
      } else {
        this.ToastrMsg.error(res?.returnString);
      }
      this.spinner.hide();
    });
  }

  // get form values to update
  toEditJobInfo(employeeJobInfo: IEmpJobInfoGetVM) {
    this.myForm.patchValue(employeeJobInfo);
    this.formCtrls?.DepartmentId.setValue(employeeJobInfo.Department.Id);
    this.formCtrls?.JobTitleId.setValue(employeeJobInfo.JobTitle.Id);
  }

  // update employee JobInfo
  updateJobInfo(employeeJobInfo: IEmpJobInfoAddEditVM) {
    this.spinner.show();
    this.EmpJobInfoServ.updateEmpJobInfo(
      this.EmployeeID,
      employeeJobInfo
    ).subscribe((res: any) => {
      if (res?.returnValue == 1) {
        let i = this.dataSource.data.findIndex(
          (e) => e.Id == this.formCtrls.Id.value
        );
        this.dataSource.data[i] = res;

        this.ToastrMsg.success("General.UpdeteSuccessfully");
        this.myForm.reset();
        this.submitted = false;
      } else {
        this.ToastrMsg.error(res?.returnString);
      }
      this.spinner.hide();
    });
  }

  // delete dialog
  openDeleteDialog(employeeJobInfo: IEmpJobInfoGetVM): void {
    let title = `${employeeJobInfo.FromDate} - ${employeeJobInfo.ToDate}`;

    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: title,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.deleteJobInfo(employeeJobInfo);
      });
  }

  // delete employeeJobInfo
  deleteJobInfo(employeeJobInfo: IEmpJobInfoGetVM) {
    this.spinner.show();
    this.EmpJobInfoServ.deleteEmpJobInfo(employeeJobInfo.Id).subscribe(
      (res: any) => {
        if (res?.returnValue == 1) {
          let i = this.dataSource.data.findIndex((e) => e == employeeJobInfo);
          this.dataSource.data.splice(i, 1);

          this.ToastrMsg.info("General.DeleteSuccessfully");
        } else {
          this.ToastrMsg.error(res?.returnString);
        }
        this.spinner.hide();
      }
    );
  }
}
