import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import {
  IUploadFPVM,
  ILoadFPResponseVM,
  IRegisterFPAddEditVM,
  // IRegisterFPGetVM,
  IRegisterFPResponseVM,
  IUploadFPResponseVM,
} from "app/dashboard/models/employees/fingerprint";
import { environment } from "environments/environment";
import { Observable } from "rxjs";

@Injectable({
  providedIn: "root",
})
export class FingerprintService {
  constructor(private http: HttpClient) {}

  //#region register Fingerprints
  // Return All RegisterFingerprints
  getAllRegisterFPs(page: number): Observable<IRegisterFPResponseVM> {
    return this.http.get<IRegisterFPResponseVM>(
      `${environment.baseUrl}RegisterFingerprints/GetAllRegisterFingerprints/${page}`
    );
  }

  // // Return RegisterFingerprint ById
  // getRegisterFPById(id: number): Observable<IRegisterFPGetVM> {
  //   return this.http.get<IRegisterFPGetVM>(
  //     `${environment.baseUrl}RegisterFingerprints/getRegisterFingerprintById/${id}`
  //   );
  // }

  // add RegisterFingerprint
  addRegisterFP(registerFingerprint: IRegisterFPAddEditVM) {
    return this.http.post(
      `${environment.baseUrl}RegisterFingerprints/addRegisterFingerprint`,
      registerFingerprint
    );
  }

  // update RegisterFingerprint
  updateRegisterFP(id: number, registerFingerprint: IRegisterFPAddEditVM) {
    return this.http.put(
      `${environment.baseUrl}RegisterFingerprints/updateRegisterFingerprint/${id}`,
      registerFingerprint
    );
  }

  // delete RegisterFingerprint
  deleteRegisterFP(id: number) {
    return this.http.delete(
      `${environment.baseUrl}RegisterFingerprints/deleteRegisterFingerprint/${id}`
    );
  }
  //#endregion

  //#region loadFingerprints
  // get all device
  getAllDevices(): Observable<any[]> {
    return this.http.get<any[]>(
      `${environment.baseUrl}LoadFingerprints/getAllDevices`
    );
  }

  // Return All LoadFingerprints
  getAllLoadFPs(page: number): Observable<ILoadFPResponseVM> {
    return this.http.get<ILoadFPResponseVM>(
      `${environment.baseUrl}LoadFingerprints/GetAllLoadFingerprints/${page}`
    );
  }

  // delete LoadFingerprint
  deleteLoadFP(id: number) {
    return this.http.delete(
      `${environment.baseUrl}LoadFingerprints/deleteLoadFingerprint/${id}`
    );
  }

  // add LoadFingerprint
  uploadFP(uploadFingerprint: IUploadFPVM) {
    return this.http.post(
      `${environment.baseUrl}LoadFingerprints/addLoadFingerprint`,
      uploadFingerprint
    );
  }

  // // Return All LoadFingerprints
  // getAllUploadFPs(page: number): Observable<IUploadFPResponseVM> {
  //   return this.http.get<IUploadFPResponseVM>(
  //     `${environment.baseUrl}LoadFingerprints/GetAllLoadFingerprints/${page}`
  //   );
  // }
  //#endregion
}
