import { Component, OnInit, ViewChild } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { MatDialog } from "@angular/material/dialog";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { ActivatedRoute } from "@angular/router";
import { TranslateService } from "@ngx-translate/core";
import {
  IRegisterFPGetVM,
  IRegisterFPAddEditVM,
} from "app/dashboard/models/employees/fingerprint";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { MessagesService } from "app/shared/services/messages.service";
import { IRowFunctionVM } from "app/shared/models/general/general";
import { NgxSpinnerService } from "ngx-spinner";
import { AuthService } from "app/shared/services/auth/auth.service";
import { IEmployeeVM } from "app/dashboard/models/employees/employee";
import { EmployeeService } from "app/dashboard/services/employees/employee.service";
import { FingerprintService } from "app/dashboard/services/employees/fingerprint.service";
import { IEmployeesVM } from "app/dashboard/models/control/control-employee";

@Component({
  selector: "app-register-fingerprint",
  templateUrl: "./register-fingerprint.component.html",
  styleUrls: ["./register-fingerprint.component.scss"],
})
export class RegisterFingerprintComponent implements OnInit {
  empFPs: IRegisterFPGetVM[] = [
    {
      Id: 1,
      EduCompId: 2,
      Employee: {
        Address: "n",
        Code: "sd",
        CountryID: 1,
        EduCompId: 2,
        Email: "bn",
        Id: 1,
        Mobile: "011521555",
        Name: "ss",
        ar_name: "ss",

        en_name: "ss",

      },
      Code: "ss",
      Date: new Date(),
      Notes: "Dd",
    },
  ];

  Employees: any[] = [
    {
      Address: "n",
      Code: "sd",
      CountryID: 1,
      EduCompId: 2,
      Email: "bn",
      Id: 1,
      Mobile: "011521555",
      Name: "ss",
    },
  ];

  myForm: FormGroup;
  submitted = false;
  curPage: number = 1;
  itemsCounts: number;
  currentLang: string;

  EduCompId: number;
  functionId: number;
  // rowFunctions: IRowFunctionVM[];
  rowFunctions: any[] = [{ functionName: "Edit" }, { functionName: "Delete" }];

  // table
  displayedColumns: string[] = [
    "#",
    "Employee",
    "Code",
    "Date",
    "Notes",
    "actions",
  ];
  dataSource: MatTableDataSource<IRegisterFPGetVM>;

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(
    private fb: FormBuilder,
    private FingerprintServ: FingerprintService,
    private EmployeeServ: EmployeeService,
    private translate: TranslateService,
    private spinner: NgxSpinnerService,
    private ToastrMsg: MessagesService,
    private dialog: MatDialog,
    private route: ActivatedRoute,
    public authserv: AuthService
  ) {
    //use after auth for function id and row functions
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      // console.log("functionId", this.functionId);
      if (this.functionId) {
        this.authserv
          .getUserRowFunctions(this.functionId)
          .subscribe((res: any) => {
            if (res.length > 0) {
              // console.log("rowFunctions", res);
              this.rowFunctions = res;
            }
          });
      }
    });

    // current language
    this.translate.onLangChange.subscribe((lang) => {
      this.currentLang = lang.lang;
    });

    this.EduCompId = Number(authserv.getEduCompId());
  }

  ngOnInit(): void {
    this.getAllempFPs(1);
    this.getAllEmployees();

    this.myForm = this.fb.group({
      Id: [null],
      EmployeeId: ["", [Validators.required]],
      Date: ["", [Validators.required]],
      FingerPrint: ["", [Validators.required]],
      Notes: [""],
      IsAbsent: ["" /* , [Validators.required] */],
    });

    // test
    this.dataSource = new MatTableDataSource(this.empFPs);
  }

  ngAfterViewInit() {
    if (this.dataSource) {
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    }

    this.authserv.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      this.getAllempFPs(1);
      this.getAllEmployees();
    });
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  get formCtrls() {
    return this.myForm.controls;
  }

  // get all employee empFPs
  getAllempFPs(page: number) {
    this.curPage = page;
    this.spinner.show();
    this.FingerprintServ.getAllRegisterFPs(page).subscribe((res) => {
      if (res) {
        this.empFPs = res.FingerprintsList;
        this.dataSource = new MatTableDataSource(this.empFPs);
        this.itemsCounts = res.countItems;
        console.log("empFPs: >> ", res);
      }
      this.spinner.hide();
    });
  }

  getAllEmployees() {
    this.spinner.show();
    this.EmployeeServ.getEmployeeEmployeesDropDown().subscribe((res) => {
      if (res) this.Employees = res;
      this.spinner.hide();
    });
  }

  onSubmit() {
    this.submitted = true;
    if (this.myForm.valid) {
      let empFP = {} as IRegisterFPAddEditVM;
      empFP.EduCompId = this.EduCompId;
      empFP.EmployeeId = this.formCtrls.EmployeeId?.value;
      empFP.Date = this.formCtrls.Date?.value;
      empFP.FingerPrint = this.formCtrls.FingerPrint?.value;
      empFP.Notes = this.formCtrls.Notes?.value;
      empFP.IsAbsent = this.formCtrls.IsAbsent?.value;
      console.log("empFP", empFP);

      this.formCtrls.Id?.value ? this.updateempFP(empFP) : this.addempFP(empFP);
    } else this.ToastrMsg.error("General.FormNotValid");
  }

  // add employee empFP
  addempFP(empFP: IRegisterFPAddEditVM) {
    this.spinner.show();
    this.FingerprintServ.addRegisterFP(empFP).subscribe((res: any) => {
      if (res?.returnValue == 1) {
        this.dataSource.data.push(res);
        this.itemsCounts++;
        // this.getAllempFPs(this.curPage);
        this.ToastrMsg.success("General.AddSuccessfully");
        this.myForm.reset();
        this.submitted = false;
      } else {
        this.ToastrMsg.error(res?.returnString);
      }
      this.spinner.hide();
    });
  }

  // get form values to update
  toEditempFP(empFP: IRegisterFPGetVM) {
    this.myForm.patchValue(empFP);
    this.formCtrls?.EmployeeId.setValue(empFP.Employee.Id);
  }

  // update employee empFP
  updateempFP(empFP: IRegisterFPAddEditVM) {
    this.spinner.show();
    this.FingerprintServ.updateRegisterFP(
      this.formCtrls?.Id?.value,
      empFP
    ).subscribe((res: any) => {
      if (res?.returnValue == 1) {
        let i = this.dataSource.data.findIndex(
          (e) => e.Id == this.formCtrls.Id.value
        );
        this.dataSource.data[i] = res;
        this.ToastrMsg.success("General.UpdeteSuccessfully");
        this.myForm.reset();
        this.submitted = false;
      } else {
        this.ToastrMsg.error(res?.returnString);
      }
      this.spinner.hide();
    });
  }

  // delete dialog
  openDeleteDialog(empFP: IRegisterFPGetVM): void {
    let title = `${empFP.Code} - ${empFP.Employee.Name}`;

    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: title,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.deleteempFP(empFP);
      });
  }

  // delete empFP
  deleteempFP(empFP: IRegisterFPGetVM) {
    this.spinner.show();
    this.FingerprintServ.deleteRegisterFP(empFP.Id).subscribe((res: any) => {
      if (res?.returnValue == 1) {
        let i = this.dataSource.data.findIndex((e) => e == empFP);
        this.dataSource.data.splice(i, 1);
        this.ToastrMsg.info("General.DeleteSuccessfully");
      } else {
        this.ToastrMsg.error(res?.returnString);
      }
      this.spinner.hide();
    });
  }
}
