import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import {
  IEmpJobInfoAddEditVM,
  IEmpJobInfoGetResponseVM,
} from "app/dashboard/models/employees/employee";
import { environment } from "environments/environment";
import { Observable } from "rxjs";

@Injectable({
  providedIn: "root",
})
export class EmpJobInfoService {
  constructor(private http: HttpClient) {}

  // Return All EmpJobInfos
  getAllJobInfosByEmployeeID(
    page: number,
    empId: number
  ): Observable<IEmpJobInfoGetResponseVM> {
    return this.http.get<IEmpJobInfoGetResponseVM>(
      `${environment.baseUrl}EmpJobInfos/GetAllEmpJobInfos/${page}/${empId}`
    );
  }

  // add EmpJobInfo
  addEmpJobInfo(empId: number, empJobInfo: IEmpJobInfoAddEditVM) {
    return this.http.post(
      `${environment.baseUrl}EmpJobInfos/addEmpJobInfo/${empId}`,
      empJobInfo
    );
  }

  // update EmpJobInfo
  updateEmpJobInfo(empId: number, empJobInfo: IEmpJobInfoAddEditVM) {
    return this.http.put(
      `${environment.baseUrl}EmpJobInfos/updateEmpJobInfo/${empId}`,
      empJobInfo
    );
  }

  // delete EmpJobInfo
  deleteEmpJobInfo(id: number) {
    return this.http.delete(
      `${environment.baseUrl}EmpJobInfos/deleteEmpJobInfo/${id}`
    );
  }
}
