import { AfterViewInit, Component, OnInit, ViewChild } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { MatDialog } from "@angular/material/dialog";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { ActivatedRoute } from "@angular/router";
import { TranslateService } from "@ngx-translate/core";
import {
  JobTitleAddEditVM,
  JobTitleGetVM,
} from "app/dashboard/models/employees/job-title";
import { JobTitleService } from "app/dashboard/services/employees/job-title.service";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { MessagesService } from "app/shared/services/messages.service";
import { IRowFunctionVM } from "app/shared/models/general/general";
import { NgxSpinnerService } from "ngx-spinner";
import { AuthService } from "app/shared/services/auth/auth.service";
import { CustomeValidator } from "app/shared/validators/customeValid.validator";

@Component({
  selector: "app-job-title",
  templateUrl: "./job-title.component.html",
  styleUrls: ["./job-title.component.scss"],
})
export class JobTitleComponent implements OnInit {
  JobTitles: JobTitleGetVM[] = [
    { Id: 1, Ar_Name: "", En_Name: "ff", EduCompId: 2 },
  ];
  myForm: FormGroup;
  submitted = false;
  curPage: number = 1;
  itemsCounts: number;
  currentLang: string;

  EduCompId: number;
  functionId: number;
  // rowFunctions: IRowFunctionVM[];
  rowFunctions: any[] = [{ functionName: "Edit" }, { functionName: "Delete" }];

  // table
  displayedColumns: string[] = ["#", "Ar_Name", "En_Name", "actions"];
  dataSource: MatTableDataSource<JobTitleGetVM>;

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(
    private fb: FormBuilder,
    private JobTitleServ: JobTitleService,
    private translate: TranslateService,
    private spinner: NgxSpinnerService,
    private ToastrMsg: MessagesService,
    private dialog: MatDialog,
    private route: ActivatedRoute,
    public authserv: AuthService
  ) {
    //use after auth for function id and row functions
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      // console.log("functionId", this.functionId);
      if (this.functionId) {
        this.authserv
          .getUserRowFunctions(this.functionId)
          .subscribe((res: any) => {
            if (res.length > 0) {
              // console.log("rowFunctions", res);
              this.rowFunctions = res;
            }
          });
      }
    });

    // current language
    this.translate.onLangChange.subscribe((lang) => {
      this.currentLang = lang.lang;
    });

    this.EduCompId = Number(authserv.getEduCompId());
  }

  ngOnInit(): void {
    this.getAllEmployeeJobTitles(1);

    this.myForm = this.fb.group({
      Id: [null],
      Ar_Name: ["", [Validators.required, CustomeValidator.whiteSpace]],
      En_Name: ["", [Validators.required, CustomeValidator.whiteSpace]],
    });

    // test data
    this.dataSource = new MatTableDataSource(this.JobTitles);
  }

  ngAfterViewInit() {
    if (this.dataSource) {
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    }

    this.authserv.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      this.getAllEmployeeJobTitles(1);
    });
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  get formCtrls() {
    return this.myForm.controls;
  }

  // get all jobTitles
  getAllEmployeeJobTitles(page: number) {
    this.curPage = page;
    this.spinner.show();

    this.JobTitleServ.getAllEmployeeJobTitles(page).subscribe((res) => {
      if (res) {
        this.JobTitles = res.JobTitleList;
        this.dataSource = new MatTableDataSource(this.JobTitles);
        this.itemsCounts = res.countItems;
        console.log("JobTitles: >> ", res);
      }
      this.spinner.hide();
    });
  }

  onSubmit() {
    this.submitted = true;
    if (this.myForm.valid) {
      let jobTitle = {} as JobTitleAddEditVM;
      jobTitle.EduCompId = this.EduCompId;
      jobTitle.Ar_Name = this.formCtrls.Ar_Name?.value;
      jobTitle.En_Name = this.formCtrls.En_Name?.value;
      console.log("jobTitle", jobTitle);

      this.formCtrls.Id?.value
        ? this.updateJobTitle(jobTitle)
        : this.addJobTitle(jobTitle);
    } else this.ToastrMsg.error("General.FormNotValid");
  }

  // add jobTitle
  addJobTitle(jobTitle: JobTitleAddEditVM) {
    this.spinner.show();
    this.JobTitleServ.addEmployeeJobTitle(jobTitle).subscribe((res: any) => {
      if (res?.returnValue == 1) {
        this.dataSource.data.push(res);
        this.itemsCounts++;
        // this.getAllEmployeeJobTitles(this.curPage);
        this.ToastrMsg.success("General.AddSuccessfully");

        this.myForm.reset();
        this.submitted = false;
      } else {
        this.ToastrMsg.error(res?.returnString);
      }
      this.spinner.hide();
    });
  }

  // get form values to update
  toEditJobTitle(jobTitle: JobTitleGetVM) {
    this.myForm.patchValue(jobTitle);
  }

  // update jobTitle
  updateJobTitle(jobTitle: JobTitleAddEditVM) {
    this.spinner.show();
    this.JobTitleServ.updateEmployeeJobTitle(
      this.formCtrls.Id.value,
      jobTitle
    ).subscribe((res: any) => {
      if (res?.returnValue == 1) {
        let i = this.dataSource.data.findIndex(
          (e) => e.Id == this.formCtrls.Id.value
        );
        this.dataSource.data[i] = res;

        this.ToastrMsg.success("General.UpdateSuccessfully");
        this.myForm.reset();
        this.submitted = false;
      } else {
        this.ToastrMsg.error(res?.returnString);
      }
      this.spinner.hide();
    });
  }

  // delete dialog
  openDeleteDialog(jobTitle: JobTitleGetVM): void {
    let jobTitleName =
      this.currentLang == "ar" ? jobTitle.Ar_Name : jobTitle.En_Name;

    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: jobTitleName,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.deleteJobTitle(jobTitle);
      });
  }

  // delete jobTitle
  deleteJobTitle(jobTitle: JobTitleGetVM) {
    this.spinner.show();
    this.JobTitleServ.deleteEmployeeJobTitle(jobTitle.Id).subscribe(
      (res: any) => {
        if (res?.returnValue == 1) {
          let i = this.dataSource.data.findIndex((e) => e == jobTitle);
          this.dataSource.data.splice(i, 1);

          this.ToastrMsg.info("General.DeleteSuccessfully");
        } else {
          this.ToastrMsg.error(res?.returnString);
        }
        this.spinner.hide();
      }
    );
  }
}
