import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";
import { DepartmentComponent } from "app/dashboard/pages/employees/department/department.component";
import { JobTitleComponent } from "app/dashboard/pages/employees/job-title/job-title.component";
import { AddEmployeeComponent } from "./employee/add-employee/add-employee.component";
import { EmployeeIndexComponent } from "./employee/employee-index/employee-index.component";
import { LoadFingerprintIndexComponent } from "app/dashboard/pages/employees/attendance/load-fingerprint-index/load-fingerprint-index.component";
import { RegisterFingerprintComponent } from "app/dashboard/pages/employees/attendance/register-fingerprint/register-fingerprint.component";
import { UploadFingerprintComponent } from "./attendance/upload-fingerprint/upload-fingerprint.component";

const routes: Routes = [
  {
    path: "employee",
    children: [
      {
        path: "",
        redirectTo: "index",
        pathMatch: "full",
      },
      {
        path: "index",
        component: EmployeeIndexComponent,
      },
      {
        path: "edit-employee/:employeeId",
        component: AddEmployeeComponent,
      },
      {
        path: "add-employee",
        component: AddEmployeeComponent,
      },
    ],
  },
  {
    path: "department",
    component: DepartmentComponent,
  },
  {
    path: "job-title",
    component: JobTitleComponent,
  },
  {
    path: "attendance",
    children: [
      {
        path: "load-FP",
        component: LoadFingerprintIndexComponent,
      },
      // {
      //   path: "upload-FP",
      //   component: UploadFingerprintComponent,
      // },
      {
        path: "register-FP",
        component: RegisterFingerprintComponent,
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class EmployeesRoutingModule {}
