import { Component, OnInit } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { ActivatedRoute } from "@angular/router";
import { TranslateService } from "@ngx-translate/core";
import { IEmployeeVM } from "app/dashboard/models/employees/employee";
import { EmployeeService } from "app/dashboard/services/employees/employee.service";
import { AuthService } from "app/shared/services/auth/auth.service";
import { MessagesService } from "app/shared/services/messages.service";
import { NgxSpinnerService } from "ngx-spinner";
import { CustomeValidator } from "app/shared/validators/customeValid.validator";
import { country } from "app/shared/models/general/general";

@Component({
  selector: "app-add-employee",
  templateUrl: "./add-employee.component.html",
  styleUrls: ["./add-employee.component.scss"],
})
export class AddEmployeeComponent implements OnInit {
  Employee: IEmployeeVM;
  EmployeeID: number;

  Countries: country[];

  myForm: FormGroup;
  submitted = false;
  currentLang: string;
  EduCompId: number;
  functionId: number;

  constructor(
    private translate: TranslateService,
    private route: ActivatedRoute,
    public authserv: AuthService,
    private fb: FormBuilder,
    private EmployeeServ: EmployeeService,
    private spinner: NgxSpinnerService,
    private ToastrMsg: MessagesService
  ) {
    //use after auth for function id and row functions
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      // console.log("functionId", this.functionId);
    });

    route.paramMap.subscribe((params) => {
      let id = Number(params.get("employeeId"));
      if (id) {
        this.EmployeeID = id;
      }
    });

    // current language
    this.translate.onLangChange.subscribe((lang) => {
      this.currentLang = lang.lang;
    });

    this.EduCompId = Number(authserv.getEduCompId());
  }

  ngOnInit(): void {
    if (this.EmployeeID) this.getEmployeeById();
    this.getAllCountries();

    this.myForm = this.fb.group({
      Code: ["", [Validators.required, CustomeValidator.whiteSpace]],
      Name: ["", [Validators.required, CustomeValidator.whiteSpace]],
      CountryID: ["", [Validators.required]],
      Address: ["", [Validators.required, CustomeValidator.whiteSpace]],
      Mobile: [
        "",
        [
          Validators.required,
          Validators.pattern(/^(010|011|012|015)[0-9]{8}$/),
          CustomeValidator.whiteSpace,
        ],
      ],
      Email: ["", [Validators.required, Validators.email]],
    });
  }

  ngAfterViewInit() {
    this.authserv.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      if (this.EmployeeID) this.getEmployeeById();
      this.getAllCountries();
    });
  }

  get formCtrls() {
    return this.myForm.controls;
  }

  // get educational years
  getAllCountries() {
    this.spinner.show();
    this.EmployeeServ.getAllCountries().subscribe((res) => {
      if (res) this.Countries = res;
      this.spinner.hide();
    });
  }

  // get employee info if edit
  getEmployeeById() {
    this.spinner.show();
    this.EmployeeServ.getEmployeeById(this.EmployeeID).subscribe((res) => {
      if (res) {
        this.Employee = res;
        this.myForm.patchValue(this.Employee);
        // this.formCtrls.CountryID?.setValue(
        //   this.Employee.Country.country_id
        // );
      }
      this.spinner.hide();
    });
  }

  onSubmit() {
    this.submitted = true;

    if (this.myForm.valid) {
      let employeeInfo = {} as IEmployeeVM;
      employeeInfo.EduCompId = this.EduCompId;
      employeeInfo.Name = this.formCtrls.Name?.value;
      employeeInfo.Code = this.formCtrls.Code?.value;
      employeeInfo.CountryID = this.formCtrls.CountryID?.value;
      employeeInfo.Address = this.formCtrls.Address?.value;
      employeeInfo.Mobile = this.formCtrls.Mobile?.value;
      employeeInfo.Email = this.formCtrls.Email?.value;
      console.log("Employee", employeeInfo);

      this.EmployeeID
        ? this.updateEmployee(employeeInfo)
        : this.addEmployee(employeeInfo);
    } else this.ToastrMsg.error("General.FormNotValid");
  }

  // add employee Info
  addEmployee(employeeInfo: IEmployeeVM) {
    this.spinner.show();
    this.EmployeeServ.addEmployee(employeeInfo).subscribe((res: any) => {
      if (res?.returnValue == 1) {
        // get employee id after add
        this.EmployeeID = res.ID;
        this.ToastrMsg.success("General.AddSuccessfully");
        this.submitted = false;
        // this.getEmployeeById();
      } else {
        this.ToastrMsg.error(res?.returnString);
      }
      this.spinner.hide();
    });
  }

  // update employee Info
  updateEmployee(employeeInfo: IEmployeeVM) {
    this.spinner.show();
    this.EmployeeServ.updateEmployee(this.EmployeeID, employeeInfo).subscribe(
      (res: any) => {
        if (res?.returnValue == 1) {
          this.ToastrMsg.success("General.UpdateSuccessfully");
          this.submitted = false;
          // this.getEmployeeById();
        } else {
          this.ToastrMsg.error(res?.returnString);
        }
        this.spinner.hide();
      }
    );
  }
}
