import { CourseGetVM } from "app/dashboard/models/setup/educational-setup/course";
import { Component, Input, OnInit, ViewChild } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { MatDialog } from "@angular/material/dialog";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { ActivatedRoute } from "@angular/router";
import { TranslateService } from "@ngx-translate/core";
import {
  IEmpCourseAddEditVM,
  IEmpCourseGetVM,
} from "app/dashboard/models/employees/employee";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { MessagesService } from "app/shared/services/messages.service";
import { IRowFunctionVM } from "app/shared/models/general/general";
import { NgxSpinnerService } from "ngx-spinner";
import { AuthService } from "app/shared/services/auth/auth.service";
import { EmpCourseService } from "app/dashboard/services/employees/emp-course.service";
import { CourseService } from "app/dashboard/services/setup/educational-setup/course.service";
import { ClassRoomGetVM } from "app/dashboard/models/setup/general-setup/classes/class-room";
import { EducationYearService } from "app/dashboard/services/setup/educational-setup/education-year.service";
import { ClassRoomService } from "app/dashboard/services/setup/general-setup/classes/class-room.service";
import { IEducationYear } from "app/dashboard/models/setup/educational-setup/education-year";
import { CustomeValidator } from "app/shared/validators/customeValid.validator";

@Component({
  selector: "app-employee-courses",
  templateUrl: "./employee-courses.component.html",
  styleUrls: ["./employee-courses.component.scss"],
})
export class EmployeeCoursesComponent implements OnInit {
  @Input("EmployeeID") EmployeeID: number;

  EmpCourses: IEmpCourseGetVM[] = [];

  Courses: CourseGetVM[] = [];
  EducationYears: IEducationYear[] = [];
  ClassRooms: ClassRoomGetVM[] = [];

  myForm: FormGroup;
  submitted = false;
  curPage: number = 1;
  itemsCounts: number;
  currentLang: string;

  EduCompId: number;
  functionId: number;
  // rowFunctions: IRowFunctionVM[];
  rowFunctions: any[] = [{ functionName: "Edit" }, { functionName: "Delete" }];

  // table
  displayedColumns: string[] = [
    "#",
    "Course",
    "EducationaYear",
    "ClassRoom",
    "LecturePerWeek",
    "actions",
  ];
  dataSource: MatTableDataSource<IEmpCourseGetVM>;

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(
    private fb: FormBuilder,
    private EmpCourseServ: EmpCourseService,
    private EducationYearServ: EducationYearService,
    private CourseServ: CourseService,
    private ClassRoomServ: ClassRoomService,
    private translate: TranslateService,
    private spinner: NgxSpinnerService,
    private ToastrMsg: MessagesService,
    private dialog: MatDialog,
    private route: ActivatedRoute,
    public authserv: AuthService
  ) {
    //use after auth for function id and row functions
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      // console.log("functionId", this.functionId);
      if (this.functionId) {
        this.authserv
          .getUserRowFunctions(this.functionId)
          .subscribe((res: any) => {
            if (res.length > 0) {
              // console.log("rowFunctions", res);
              this.rowFunctions = res;
            }
          });
      }
    });

    // current language
    this.translate.onLangChange.subscribe((lang) => {
      this.currentLang = lang.lang;
    });

    this.EduCompId = Number(authserv.getEduCompId());
  }

  ngOnInit(): void {
    if (this.EmployeeID) this.getAllEmpCourses(1);
    this.getAllCourses();

    this.myForm = this.fb.group({
      Id: [null],
      CourseId: ["", [Validators.required]],
      StageId: ["", [Validators.required]],
      EducationaYearId: ["", [Validators.required]],
      ClassRoomId: ["", [Validators.required]],
      LecturePerWeek: ["", [Validators.required, CustomeValidator.minusNum]],
    });

    // test
    this.dataSource = new MatTableDataSource(this.EmpCourses);
  }

  ngAfterViewInit() {
    if (this.dataSource) {
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    }

    this.authserv.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      if (this.EmployeeID) this.getAllEmpCourses(1);
      this.getAllCourses();
    });

    console.log(this.EmployeeID);
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  get formCtrls() {
    return this.myForm.controls;
  }

  // get all employee Courses
  getAllEmpCourses(page: number) {
    this.curPage = page;
    this.spinner.show();

    this.EmpCourseServ.getAllCoursesByEmployeeID(
      page,
      this.EmployeeID
    ).subscribe((res) => {
      if (res) {
        this.EmpCourses = res.EmpCoursesList;
        this.dataSource = new MatTableDataSource(this.EmpCourses);
        this.itemsCounts = res.countItems;
        console.log("Courses: >> ", res);
      }
      this.spinner.hide();
    });
  }

  getAllCourses() {
    this.spinner.show();
    this.CourseServ.getAllCoursesDropDown().subscribe((res) => {
      if (res) this.Courses = res;
      this.spinner.hide();
    });
  }

  getAllClassRooms() {
    this.spinner.show();
    this.ClassRoomServ.getAllClassRoomsDropDown(this.EduCompId).subscribe(
      (res) => {
        if (res) this.ClassRooms = res;
        this.spinner.hide();
      }
    );
  }

  onSubmit() {
    this.submitted = true;
    if (this.myForm.valid) {
      let employeeCourse = {} as IEmpCourseAddEditVM;
      employeeCourse.EduCompId = this.EduCompId;
      employeeCourse.Id = this.formCtrls.Id?.value;
      employeeCourse.CourseId = this.formCtrls.CourseId?.value;
      // employeeCourse.StageId = this.formCtrls.StageId?.value;
      employeeCourse.EducationaYearId = this.formCtrls.EducationaYearId?.value;
      employeeCourse.ClassRoomId = this.formCtrls.ClassRoomId?.value;
      employeeCourse.LecturePerWeek = this.formCtrls.LecturePerWeek?.value;
      console.log("employeeCourse", employeeCourse);

      this.formCtrls.Id?.value
        ? this.updateEmpCourse(employeeCourse)
        : this.addEmpCourse(employeeCourse);
    } else this.ToastrMsg.error("General.FormNotValid");
  }

  // add employee Course
  addEmpCourse(employeeCourse: IEmpCourseAddEditVM) {
    this.spinner.show();
    this.EmpCourseServ.addEmpCourse(this.EmployeeID, employeeCourse).subscribe(
      (res: any) => {
        if (res?.returnValue == 1) {
          this.dataSource.data.push(res);
          this.itemsCounts++;
          // this.getAllEmpCourses(this.curPage);
          this.ToastrMsg.success("General.AddSuccessfully");

          this.formCtrls?.ClassRoomId?.setValue("");
          // this.myForm.reset();
          this.submitted = false;
        } else {
          this.ToastrMsg.error(res?.returnString);
        }
        this.spinner.hide();
      }
    );
  }

  // get form values to update
  toEditEmpCourse(employeeCourse: IEmpCourseGetVM) {
    this.myForm.patchValue(employeeCourse);
    this.formCtrls?.CourseId.setValue(employeeCourse.Course.id);
    this.formCtrls?.EducationYearId.setValue(
      employeeCourse.EducationaYear.eduYearId
    );
    this.formCtrls?.EducationYearId.setValue(
      employeeCourse.EducationaYear.eduYearId
    );
    this.formCtrls?.EducationYearId.setValue(employeeCourse.ClassRoom.id);
  }

  // update employee Course
  updateEmpCourse(employeeCourse: IEmpCourseAddEditVM) {
    this.spinner.show();
    this.EmpCourseServ.updateEmpCourse(
      this.EmployeeID,
      employeeCourse
    ).subscribe((res: any) => {
      if (res?.returnValue == 1) {
        let i = this.dataSource.data.findIndex(
          (e) => e.Id == this.formCtrls.Id.value
        );
        this.dataSource.data[i] = res;

        this.ToastrMsg.success("General.UpdeteSuccessfully");
        this.myForm.reset();
        this.submitted = false;
      } else {
        this.ToastrMsg.error(res?.returnString);
      }
      this.spinner.hide();
    });
  }

  // delete dialog
  openDeleteDialog(employeeCourse: IEmpCourseGetVM): void {
    let title =
      this.currentLang == "ar"
        ? `${employeeCourse.Course.subject_ar_name} - ${employeeCourse.EducationaYear.ar_name}`
        : `${employeeCourse.Course.subject_en_name} - ${employeeCourse.EducationaYear.en_name}`;

    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: title,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.deleteEmpCourse(employeeCourse);
      });
  }

  // delete employeeCourse
  deleteEmpCourse(employeeCourse: IEmpCourseGetVM) {
    this.spinner.show();
    this.EmpCourseServ.deleteEmpCourse(employeeCourse.Id).subscribe(
      (res: any) => {
        if (res?.returnValue == 1) {
          let i = this.dataSource.data.findIndex((e) => e == employeeCourse);
          this.dataSource.data.splice(i, 1);

          this.ToastrMsg.info("General.DeleteSuccessfully");
        } else {
          this.ToastrMsg.error(res?.returnString);
        }
        this.spinner.hide();
      }
    );
  }
}
