import { Component, Input, OnInit, ViewChild } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { MatDialog } from "@angular/material/dialog";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { ActivatedRoute } from "@angular/router";
import { TranslateService } from "@ngx-translate/core";
import { IEmpCertificationVM } from "app/dashboard/models/employees/employee";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { MessagesService } from "app/shared/services/messages.service";
import { IRowFunctionVM } from "app/shared/models/general/general";
import { NgxSpinnerService } from "ngx-spinner";
import { AuthService } from "app/shared/services/auth/auth.service";
import { EmpCertificationService } from "app/dashboard/services/employees/emp-certification.service";
import { CustomeValidator } from "app/shared/validators/customeValid.validator";

@Component({
  selector: "app-employee-certifications",
  templateUrl: "./employee-certifications.component.html",
  styleUrls: ["./employee-certifications.component.scss"],
})
export class EmployeeCertificationsComponent implements OnInit {
  @Input("EmployeeID") EmployeeID: number;

  EmpCertifications: IEmpCertificationVM[] = [
    { EduCompId: 2, Id: 2, Name: "Sx", FromDate: new Date(), Place: "dc" },
  ];

  myForm: FormGroup;
  submitted = false;
  curPage: number = 1;
  itemsCounts: number;
  currentLang: string;

  EduCompId: number;
  functionId: number;
  // rowFunctions: IRowFunctionVM[];
  rowFunctions: any[] = [{ functionName: "Edit" }, { functionName: "Delete" }];

  // table
  displayedColumns: string[] = ["#", "Name", "Place", "FromDate", "actions"];
  dataSource: MatTableDataSource<IEmpCertificationVM>;

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(
    private fb: FormBuilder,
    private EmpCertificationServ: EmpCertificationService,
    private translate: TranslateService,
    private spinner: NgxSpinnerService,
    private ToastrMsg: MessagesService,
    private dialog: MatDialog,
    private route: ActivatedRoute,
    public authserv: AuthService
  ) {
    //use after auth for function id and row functions
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      // console.log("functionId", this.functionId);
      if (this.functionId) {
        this.authserv
          .getUserRowFunctions(this.functionId)
          .subscribe((res: any) => {
            if (res.length > 0) {
              // console.log("rowFunctions", res);
              this.rowFunctions = res;
            }
          });
      }
    });

    // current language
    this.translate.onLangChange.subscribe((lang) => {
      this.currentLang = lang.lang;
    });

    this.EduCompId = Number(authserv.getEduCompId());
  }

  ngOnInit(): void {
    if (this.EmployeeID) this.getAllCertifications(1);

    this.myForm = this.fb.group({
      Id: [null],
      Name: ["", [Validators.required, CustomeValidator.whiteSpace]],
      Place: ["", [Validators.required, CustomeValidator.whiteSpace]],
      FromDate: ["", [Validators.required]],
    });

    // test
    this.dataSource = new MatTableDataSource(this.EmpCertifications);
  }

  ngAfterViewInit() {
    if (this.dataSource) {
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    }

    this.authserv.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      if (this.EmployeeID) this.getAllCertifications(1);
    });

    // console.log(this.EmployeeID);
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  get formCtrls() {
    return this.myForm.controls;
  }

  // get all employee Certifications
  getAllCertifications(page: number) {
    this.curPage = page;
    this.spinner.show();

    this.EmpCertificationServ.getAllCertificationsByEmployeeID(
      page,
      this.EmployeeID
    ).subscribe((res) => {
      if (res) {
        this.EmpCertifications = res.EmpCertificationList;
        this.dataSource = new MatTableDataSource(this.EmpCertifications);
        this.itemsCounts = res.countItems;
        console.log("Certifications: >> ", res);
      }
      this.spinner.hide();
    });
  }

  onSubmit() {
    this.submitted = true;
    if (this.myForm.valid) {
      let employeeCertification = {} as IEmpCertificationVM;
      employeeCertification.EduCompId = this.EduCompId;
      employeeCertification.Id = this.formCtrls.Id?.value;
      employeeCertification.Name = this.formCtrls.Name?.value;
      employeeCertification.Place = this.formCtrls.Place?.value;
      employeeCertification.FromDate = this.formCtrls.FromDate?.value;
      console.log("employeeCertification", employeeCertification);

      this.formCtrls.Id?.value
        ? this.updateCertification(employeeCertification)
        : this.addCertification(employeeCertification);
    } else this.ToastrMsg.error("General.FormNotValid");
  }

  // add employee Certification
  addCertification(employeeCertification: IEmpCertificationVM) {
    this.spinner.show();
    this.EmpCertificationServ.addEmpCertification(
      this.EmployeeID,
      employeeCertification
    ).subscribe((res: any) => {
      if (res?.returnValue == 1) {
        this.dataSource.data.push(res);
        this.itemsCounts++;
        // this.getAllCertifications(this.curPage);
        this.ToastrMsg.success("General.AddSuccessfully");

        this.myForm.reset();
        this.submitted = false;
      } else {
        this.ToastrMsg.error(res?.returnString);
      }
      this.spinner.hide();
    });
  }

  // get form values to update
  toEditCertification(employeeCertification: IEmpCertificationVM) {
    this.myForm.patchValue(employeeCertification);
  }

  // update employee Certification
  updateCertification(employeeCertification: IEmpCertificationVM) {
    this.spinner.show();
    this.EmpCertificationServ.updateEmpCertification(
      this.EmployeeID,
      employeeCertification
    ).subscribe((res: any) => {
      if (res?.returnValue == 1) {
        let i = this.dataSource.data.findIndex(
          (e) => e.Id == this.formCtrls.Id.value
        );
        this.dataSource.data[i] = res;

        this.ToastrMsg.success("General.UpdeteSuccessfully");
        this.myForm.reset();
        this.submitted = false;
      } else {
        this.ToastrMsg.error(res?.returnString);
      }
      this.spinner.hide();
    });
  }

  // delete dialog
  openDeleteDialog(employeeCertification: IEmpCertificationVM): void {
    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: employeeCertification.Name,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.deleteCertification(employeeCertification);
      });
  }

  // delete employeeCertification
  deleteCertification(employeeCertification: IEmpCertificationVM) {
    this.spinner.show();
    this.EmpCertificationServ.deleteEmpCertification(
      employeeCertification.Id
    ).subscribe((res: any) => {
      if (res?.returnValue == 1) {
        let i = this.dataSource.data.findIndex(
          (e) => e == employeeCertification
        );
        this.dataSource.data.splice(i, 1);

        this.ToastrMsg.info("General.DeleteSuccessfully");
      } else {
        this.ToastrMsg.error(res?.returnString);
      }
      this.spinner.hide();
    });
  }
}
