import { Component, ElementRef, Input, OnInit, ViewChild } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { MatDialog } from "@angular/material/dialog";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { ActivatedRoute } from "@angular/router";
import { TranslateService } from "@ngx-translate/core";
import {
  IAttachmentAddEditVM,
  IAttachmentGetVM,
  IAttachTypeGetDropdown,
} from "app/dashboard/models/setup/general-setup/attach-type";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { AttachTypeService } from "app/dashboard/services/setup/general-setup/attach-type.service";
import { MessagesService } from "app/shared/services/messages.service";
import { IfileVM, IRowFunctionVM } from "app/shared/models/general/general";
import { NgxSpinnerService } from "ngx-spinner";
import { AuthService } from "app/shared/services/auth/auth.service";
import { EmpAttachmentService } from "app/dashboard/services/employees/emp-attachment.service";

@Component({
  selector: "app-employee-attachments",
  templateUrl: "./employee-attachments.component.html",
  styleUrls: ["./employee-attachments.component.scss"],
})
export class EmployeeAttachmentsComponent implements OnInit {
  @Input("EmployeeID") EmployeeID: number;

  @ViewChild("fileInput") fileInput: ElementRef;

  Attachments: IAttachmentGetVM[] = [
    {
      Id: 1,
      EduCompId: 2,
      AttachmentUrl: "sx",
      AttachmentType: { Id: 1, Ar_Name: "ss", En_Name: "ax" },
    },
  ];
  AttachmentTypes: IAttachTypeGetDropdown[] = [];

  myForm: FormGroup;
  submitted = false;
  curPage: number = 1;
  itemsCounts: number;
  currentLang: string;

  EduCompId: number;
  functionId: number;
  // rowFunctions: IRowFunctionVM[];
  rowFunctions: any[] = [{ functionName: "Edit" }, { functionName: "Delete" }];

  // table
  displayedColumns: string[] = ["#", "AttachmentType", "Download", "actions"];
  dataSource: MatTableDataSource<IAttachmentGetVM>;

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(
    private fb: FormBuilder,
    private EmpAttachmentServ: EmpAttachmentService,
    private AttachTypeServ: AttachTypeService,
    private translate: TranslateService,
    private spinner: NgxSpinnerService,
    private ToastrMsg: MessagesService,
    private dialog: MatDialog,
    private route: ActivatedRoute,
    public authserv: AuthService
  ) {
    //use after auth for function id and row functions
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      // console.log("functionId", this.functionId);
      if (this.functionId) {
        this.authserv
          .getUserRowFunctions(this.functionId)
          .subscribe((res: any) => {
            if (res.length > 0) {
              // console.log("rowFunctions", res);
              this.rowFunctions = res;
            }
          });
      }
    });

    // current language
    this.translate.onLangChange.subscribe((lang) => {
      this.currentLang = lang.lang;
    });

    this.EduCompId = Number(authserv.getEduCompId());
  }

  ngOnInit(): void {
    if (this.EmployeeID) this.getAllAttachments(1);
    this.getAttachmentTypes();

    this.myForm = this.fb.group({
      Id: [null],
      Type: ["", [Validators.required]],
      File: ["", [Validators.required]],
    });

    // test
    this.dataSource = new MatTableDataSource(this.Attachments);
  }

  ngAfterViewInit() {
    if (this.dataSource) {
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    }

    this.authserv.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      if (this.EmployeeID) this.getAllAttachments(1);
      this.getAttachmentTypes();
    });
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  get formCtrls() {
    return this.myForm.controls;
  }

  // get Attachment Types
  getAttachmentTypes() {
    this.spinner.show();
    this.AttachTypeServ.getAttachTypesDropDown(this.EduCompId).subscribe(
      (res) => {
        if (res) this.AttachmentTypes = res;
        this.spinner.hide();
      }
    );
  }

  // get all attachments
  getAllAttachments(page: number) {
    this.curPage = page;
    this.spinner.show();

    this.EmpAttachmentServ.getAllAttachmentsByEmployeeID(
      page,
      this.EmployeeID
    ).subscribe((res) => {
      if (res) {
        this.Attachments = res.AttachmentList;
        this.dataSource = new MatTableDataSource(this.Attachments);
        this.itemsCounts = res.countItems;
        console.log("Attachments: >> ", res);
      }
      this.spinner.hide();
    });
  }

  // on select file
  onChangeFile(event: any) {
    let fileName = event.target.files[0].name;
    let fileSize = event.target.files[0].size;
    let fileType = event.target.files[0].type;
    let LastModified = event.target.files[0].lastModified;
    let LastModifiedDate = event.target.files[0].lastModifiedDate;

    if (event.target.files) {
      var reader = new FileReader();

      reader.readAsDataURL(event.target.files[0]);
      reader.onload = (event: any) => {
        let fileReder = event.target.result;

        let data: IfileVM = {
          FileAsBase64: fileReder.replace(
            "data:application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;base64,",
            ""
          ),
          name: fileName,
          size: fileSize,
          type: fileType,
          LastModified: LastModified,
          LastModifiedDate: LastModifiedDate,
        };
        this.formCtrls?.File?.setValue(data);
      };
    }
  }

  RemoveFile() {
    this.formCtrls.File.setValue("");
    this.fileInput.nativeElement.value = "";
  }

  onSubmit() {
    this.submitted = true;
    if (this.myForm.valid) {
      let attachment = {} as IAttachmentAddEditVM;
      attachment.EduCompId = this.EduCompId;
      attachment.Id = this.formCtrls.Id?.value;
      attachment.AttachmentTypeId = this.formCtrls.Type?.value;
      attachment.AttachmentFile = this.formCtrls.File?.value;
      console.log("attachment", attachment);

      this.formCtrls.Id?.value
        ? this.updateAttachment(attachment)
        : this.addAttachment(attachment);
    } else this.ToastrMsg.error("General.FormNotValid");
  }

  // add attachment
  addAttachment(attachment: IAttachmentAddEditVM) {
    this.spinner.show();
    this.EmpAttachmentServ.addEmpAttachment(
      this.EmployeeID,
      attachment
    ).subscribe((res: any) => {
      if (res?.returnValue == 1) {
        this.getAllAttachments(this.curPage);
        this.ToastrMsg.success("General.AddSuccessfully");

        this.myForm.reset();
        this.submitted = false;
      } else {
        this.ToastrMsg.error(res?.returnString);
      }
      this.spinner.hide();
    });
  }

  // get form values to update
  toEditAttachment(attachment: IAttachmentGetVM) {
    this.formCtrls?.Id.setValue(attachment.Id);
    this.formCtrls?.Type.setValue(attachment.AttachmentType.Id);
    this.formCtrls?.File.setValue(attachment.AttachmentUrl);
  }

  // update attachment
  updateAttachment(attachment: IAttachmentAddEditVM) {
    this.spinner.show();
    this.EmpAttachmentServ.updateEmpAttachment(
      this.EmployeeID,
      attachment
    ).subscribe((res: any) => {
      if (res?.returnValue == 1) {
        let i = this.dataSource.data.findIndex(
          (e) => e.Id == this.formCtrls.Id.value
        );
        this.dataSource.data[i] = res;

        this.ToastrMsg.success("General.UpdeteSuccessfully");
        this.myForm.reset();
        this.submitted = false;
      } else {
        this.ToastrMsg.error(res?.returnString);
      }
      this.spinner.hide();
    });
  }

  // delete dialog
  openDeleteDialog(attachment: IAttachmentGetVM): void {
    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: attachment.AttachmentType,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.deleteAttachment(attachment);
      });
  }

  // delete attachment
  deleteAttachment(attachment: IAttachmentGetVM) {
    this.spinner.show();
    this.EmpAttachmentServ.deleteEmpAttachment(attachment.Id).subscribe(
      (res: any) => {
        if (res?.returnValue == 1) {
          let i = this.dataSource.data.findIndex((e) => e == attachment);
          this.dataSource.data.splice(i, 1);

          this.ToastrMsg.info("General.DeleteSuccessfully");
        } else {
          this.ToastrMsg.error(res?.returnString);
        }
        this.spinner.hide();
      }
    );
  }
}
